<template>
  <div>
    <p>
      <b v-if="esDistritos">
        Mapa de Distritos Electorales
      </b>
      <b v-else>
        Mapa de Municipios
      </b>
    </p>
    <p>
      El mapa resalta en los
      <span v-if="esDistritos">
        Distritos
      </span>
      <span v-else>
        Municipios
      </span>
      Electorales el Partido Poltico, Coalicin o Candidatura Independiente que lleva ventaja hasta el momento.
    </p>
    <!--<img src="@/assets/Distritos.svg" alt="" class="img-fluid mx-auto" v-if="esDistritos">
    <img src="@/assets/Municipios.svg" alt="" class="img-fluid mx-auto" v-else>-->
    <SvgPanZoom 
        style="width: 100%; height: 510px; "
        :fit="true"
        :center="true"
        :mouseWheelZoomEnabled="false" 
        :panEnabled="false" 
        :draggable="false"  
        :dblClickZoomEnabled="false"
        :controlIconsEnabled="false"
        :preventMouseEventsDefault="false"
        :zoomEnabled="false"
    >
          <svg-map
            class="mapa-distritos"
            style="width:100% !important; height:100%;"
            :map="Municipios"    
            v-if="!esDistritos"
          />
          <svg-map
            class="mapa-distritos"
            style="width:100% !important; height:100%;"
            :map="Distritos"
                
            v-else
          />
    </SvgPanZoom>
  </div>
</template>

<script>
import { SvgMap } from "vue-svg-map";
import SvgPanZoom from 'vue-svg-pan-zoom';
import Distritos from "@/assets/distritos";
import Municipios from "@/assets/municipios";
export default {
  name: 'MapaDistritos',
   components: {
    SvgMap,
    SvgPanZoom 
  },
   data() {
    return {
      isMobile: /iPhone|iPad|iPod|Android/i.test(navigator.userAgent),
      Municipios,
      Distritos,
      mapa: Array,
      region: {
        type: Array,
        defautl: () => []
      },
      
      seleccionRegion: false,
      seleccion: Object,
      tooltipActivo: false,
      svgpanzoom: null,

      minX: -Infinity, // No hay lmite en el lado izquierdo
      maxX: Infinity, // No hay lmite en el lado derecho
        minY: 0, // Lmite en la parte superior
        maxY: Infinity // No hay lmite en la parte inferior

    };
  },
  props: {
    detalle: Object,
    esDistritos: {
      type: Boolean,
      default: () => false
    }
  },
  mounted: function() {
    this.cargarEventos();
    this.initSVGPanZoom();

  },
  watch: {
    esMapa: {
      handler(val) {
        this.$nextTick(() => {
          if (val) {
            this.seleccionRegion = false;
            this.cargarEventos();
          }
        });
      },
      $nextTick: true
    }
  },
  computed:{
    regionSeleccionada() {
      if (this.$route.params.eleccion === "D") {
        return this.$store.state.dVotosMapaTarjetas;
      } else if (this.$route.params.eleccion === "A") {
        return this.$store.state.aVotosMapaTarjetas;
      }
    },
    eleccion() {
      return this.$route.params.eleccion;
    },
    esMapa() {
      return this.detalle.esMapa;
    }
  },
  methods:{

    initSVGPanZoom() {
      // Obtener la referencia al elemento SVG dentro de SvgPanZoom
      const svgElement = this.$refs.svgPanZoom.$el.querySelector('.mapa-distritos svg');

      const svgPanZoom = SVGPanZoom(svgElement, {
        
        panEnabled: false,
        mouseWheelZoomEnabled: false, 
      });
    },

    registerSvgPanZoom(svgpanzoom) {
      this.svgpanzoom = svgpanzoom;
    },
    salirMapa(){
      this.svgpanzoom.resetZoom();
      this.svgpanzoom.center();
    },
    cargarEventos: function() {
       var elemento = document.getElementsByClassName("svg-map__location");

      elemento.forEach(r => {
        r.addEventListener(
          "mouseover",
          function(e) {
            this.desplegarRegion(e, r);
          }.bind(this),
          false
        );

        r.addEventListener(
          "click",
          function() {
            this.detalleMapa(r);
          }.bind(this),
          false
        );
        var color = "";
        if (this.eleccion === "D") {
          color = this.regionSeleccionada.datos_distritos.filter(
            t => t.distrito_id == r.getAttribute("finder")
          )[0].candidatura_ganadora_color;
        } else {
          color = this.regionSeleccionada.datos_municipios.filter(
            t => t.municipio_id == r.getAttribute("finder")
          )[0].candidatura_ganadora_color;
        }
        r.style.fill = color;
      }, this);

      this.mapa = elemento;
    },
    mostrarRegion: function() {
      this.seleccionRegion = false;
      this.mapa.forEach(m => {
        m.classList.remove("opacidad_mapa");
      });
    },
    detalleMapa: function(r) {
      this.$router.push(
        `/${this.eleccion}/DET/VPC/${r.getAttribute("finder")}`
      );
      this.actualizarEstado(r);
      this.detalle.datos.elemento = r;
    },
    actualizarEstado: function(elemento) {
      this.detalle.esMapa = false;
      this.detalle.region = elemento;
      this.detalle.esDiputaciones = this.esDiputaciones;
      this.$emit("childToParent", this.detalle);
    },
  }
}
</script>

<style lang="scss" scoped>
.btn-salir {
  /* position: absolute;
  right: 0px;
  margin-right: 20px; */
  float: right;
}
</style>
