<template>
  <div style="display:block;">

    <section v-if="detalle.esMapa" class="resumen-ayuntamientos ">
      <div class="grilla d-flex justify-content-center ">
        <div class="tabla">
          <div class="grilla-interna">      
          </div>
          <div
            :class="
              candidato.candidatura_id ==
              detalle.datos.candidatura_id_con_mayor_numero_votos
                ? 'grilla-interna mayor'
                : 'grilla-interna'
            "
            v-for="(candidato, index) in detalle.datos.datos_candidatos"
            :key="index"
          >
            <div class="borde h-100">
              <div class="grilla-interna mb-3">
                <div
                  class="contenedor-imagen"
                  :style="{ 'border-color': candidato.candidatura_color }"
                >
                <img
                    class="foto-candidatura"
                    :src="candidato.candidatura_logo"
                    alt=""
                  />
                </div>
                <!-- <div class="text-left mr-3">
                  <h2 class="font-weight-bold">
                    {{
                      eleccion === "D"
                        ? candidato.candidatura_distritos_obtenidos_numero
                        : candidato.candidatura_municipios_obtenidos_numero
                    }}
                  </h2>
                </div> -->
              </div>
            </div>
            <div class="grilla-interna h-100 mb-3">
              <h2 class="font-weight-bold">
                    {{
                      eleccion === "D"
                        ? candidato.candidatura_distritos_obtenidos_numero
                        : candidato.candidatura_municipios_obtenidos_numero
                    }}
                  </h2>
              <!-- <h class="font-weight-bold mb-0">
                {{ candidato.candidatura_total_votos_numero }}
              </h> -->
              <!-- <div>
                <p class="text-left mb-0">
                  {{ candidato.candidatura_total_votos_porcentaje }}%
                </p>
                <b-progress max="100">
                  <b-progress-bar
                    :value="candidato.candidatura_total_votos_porcentaje"
                    :style="{ 'background-color': candidato.candidatura_color }"
                  />
                </b-progress>
              </div> -->
            </div>
          </div>

          <div class=" mt-3">
            <div class="grilla-interna">
              <h4 class="text-left font-weight-bold">
                Total
              </h4>
              <h2 class="text-center font-weight-bold mr-5">
                {{ calcularTotal }}
              </h2>
            </div>
          </div>

        </div>
        <div class="mapa h-100 ml-5">
          <p class="mensaje" v-if="!esDiputaciones">
            <b>
              Mapa de Municipios
            </b>
            <br />
            El mapa resalta en los Municipios el Partido Poltico que llevan ventaja  hasta el momento.
          </p>
          <p class="mensaje" v-else>
            <b>
              Mapa de Distritos Electorales
            </b>
            <br />
            El mapa resalta en los Distritos Electorales el Partido Poltico 
            que llevan ventaja hasta el momento.
          </p>
          <SvgPanZoom
            style="height: 650px; width: 600px; "
            :fit="true"
            :center="true"
            @dblclick.prevent  
            :panEnabled="false"
            :draggable="false"
            :zoomEnabled="false"
            :dblClickZoomEnabled="false"
            :controlIconsEnabled="false"
            :mouseWheelZoomEnabled="false"
            :preventMouseEventsDefault="false"
          >
            <svg-map
              class="mapa-distritos"
              style="width:100% !important; height:100%;"
              :map="Distritos"
              v-if="esDiputaciones"
             
            />
            <svg-map
              class="mapa-distritos"
              style="width:100% !important; height:100%;"
              :map="Municipios"
              v-else
            />
          </SvgPanZoom>
          
        </div>
      </div>

      <!--Insercion de nueva tabla -->
      <div class="votos-cabecera">
        <div class="titulos mt-5">
          <h3>Votos en Actas Contabilizadas</h3>

          <b class= "p-4">
            En la tabla se muestran los votos que cada Partido Poltico o Candidatura Independiente ha obtenido hasta el momento.
          </b>


        </div>
      </div>
     <!-- Tabla de votos en actas contabilizadas -->
      <div class="table-responsive text-center ">
        <table class="table table-striped">
          <thead>
          <th></th>
          <th v-for="(candidato, index) in votosXPartidoGraficas.partidos"
            :key="index">
            <span style="font-size: .5rem;" v-if="candidato.partido_nombre=='CANDIDATURAS NO REGISTRADAS'||candidato.partido_nombre=='NULOS'"> {{ (candidato.partido_nombre=='NULOS'?`VOTOS ${candidato.partido_nombre}`:candidato.partido_nombre) }}</span>

            <img
              class="foto-candidatura-tabla"
              :src="candidato.partido_logo"
              alt=""
            />

         </th>
          </thead>

          <tbody >
            <tr class="font-weight-bold">
              <td>Total de Votos </td>

              <td v-for="(partido, index) in votosXPartidoGraficas.partidos" :key="index">

                <h5>
                  <strong>
                    {{(partido.partido_total_votos_general_numero.toLocaleString('en-US')|| 0) }}
                  </strong>
                </h5>

            </td>
            </tr>

            <tr>
              <td>Porcentaje </td>
              <td v-for="(partido, index) in votosXPartidoGraficas.partidos" :key="index">
                <h6>{{partido.partido_total_votos_general_porcentaje ||0}}%</h6>
            </td>
            </tr>

          </tbody>


        </table>
      </div>


      <div class="row" v-if="eleccion === 'D'">
        <div class="col-12 mt-4 ">
          <div class="" >
            <b class= "p-4">
              No se considera la cantidad de votos asentada en las
              Actas PREP de la Casilla Especial de la Representacin Proporcional. <br/>
             <b class= "p-4">
             Por presentacin, los decimales de los porcentajes muestran slo cuatro dgitos.
             No obstante, al considerar todos los decimales, suman 100%.
             </b>
            </b>
          </div>
        </div>
      </div>
      <div class="row" v-else-if="eleccion === 'A'">
        <div class="col-12 mt-4 ">
          <div>
            <b class= "p-4">

             Por presentacin, los decimales de los porcentajes muestran slo cuatro dgitos.
             No obstante, al considerar todos los decimales, suman 100%.

            </b>
          </div>
        </div>
      </div>


    </section>

    <ResumenTarjetasAyuntamientos
      v-else
      :detalle="detalle.datos"
    ></ResumenTarjetasAyuntamientos>

  </div>

</template>

<script>
import 'vue-svg-map/dist/index.css';
import { SvgMap } from 'vue-svg-map';
import SvgPanZoom from 'vue-svg-pan-zoom';
import Distritos from '@/assets/distritos';
import Municipios from '@/assets/municipios';
import Tooltip from '@/components/Escritorio/componentes/Tooltip.vue';
import ResumenTarjetasAyuntamientos from './ResumenTarjetasAyuntamientos.vue';

export default {
  name: 'ResumenMapaAyuntamiento',
  components: {
    SvgMap,
    Tooltip,
    ResumenTarjetasAyuntamientos,
    SvgPanZoom,
  },
  data() {
    return {
      Municipios,
      Distritos,
      selectedLocation: null,
      mapa: Array,
      region: {
        type: Array,
        defautl: () => [],
      },
      seleccionRegion: false,
      seleccion: Object,
      tooltipActivo: false,
      svgpanzoom: null,
    };
  },
  props: {
    detalle: Object,
    // esDistritos: {
    esDiputaciones: {
      type: Boolean,
      default: () => false,
    },
    
  },
  mounted() {
    this.cargarEventos();
    this.initSVGPanZoom();

  },
  methods: {
    initSVGPanZoom() {
      // Obtener la referencia al elemento SVG dentro de SvgPanZoom
      const svgElement = this.$refs.svgPanZoom.$el.querySelector('.mapa-distritos svg');
      const svgPanZoom = SVGPanZoom(svgElement, {        
        panEnabled: false,
        mouseWheelZoomEnabled: false, 
      });
    },
    registerSvgPanZoom(svgpanzoom) {
      this.svgpanzoom = svgpanzoom;
    },

    cargarEventos: function() {
      const elemento = document.getElementsByClassName('svg-map__location');
      // if (Array.isArray(elemento)) {
        elemento.forEach((r) => {
        r.addEventListener(
          'mouseover',
          (e) => {
            this.desplegarRegion(e, r);
          },
          false,
        );

        r.addEventListener(
          'click',
          () => {
            this.detalleMapa(r);
          },
          false,
        );
        let color = '';
        if (this.eleccion === 'D') {
          color = this.regionSeleccionada?.datos_distritos.filter(
            (t) => t.distrito_id == r.getAttribute('finder'),
          )[0].candidatura_ganadora_color;
        } if (this.eleccion === 'A') {
          color = this.regionSeleccionada.datos_municipios.filter(
            (t) => t.municipio_id == r.getAttribute('finder'),
          )[0].candidatura_ganadora_color;
        }
        r.style.fill = color;
      }, this);

        this.mapa = elemento;
      // }else{
      //   console.log("El elemento no es un array.");
      // }
    },
    obtenerRegionTooltip(id) {
      const distrito = this.detalle.datos.datos_candidatos.filter(
        (r) => r.candidatura_id == id,
      )[0];
      return distrito;
    },
    candidaturaGanadora(id) {
      let idGanadora;
      if (this.eleccion === 'D') {
        idGanadora = this.regionSeleccionada.datos_distritos.filter(
          (t) => t.distrito_id == id,
        )[0].candidatura_id_ganadora;
      } else {
        idGanadora = this.regionSeleccionada.datos_municipios.filter(
          (t) => t.municipio_id == id,
        )[0].candidatura_id_ganadora;
      }
      return idGanadora;
    },
    desplegarRegion(e, r) {
      if (this.seleccionRegion) {
        this.mostrarRegion();
      }
      this.$nextTick(() => {
        this.region.estado = true;
        this.region.map = r;
        const candidaturaGanadora = this.candidaturaGanadora(r.getAttribute('finder'));
        if (candidaturaGanadora != 0) {
          this.region.datos = this.obtenerRegionTooltip(candidaturaGanadora);
          this.region.datos.ganadora = true;
        } else {
          this.region.datos = undefined;
        }
        this.region.event = e;
        this.region.tipo = this.detalle.eleccion;
        // this.region.esDiputaciones = this.esDiputaciones;
        this.seleccionRegion = true;
        this.mapa.forEach((m) => {
          if (m.id != r.id) {
            m.classList.add('opacidad_mapa');
          } else {
            m.classList.remove('opacidad_mapa');
          }
        });
      });
    },
    mostrarRegion() {
      this.seleccionRegion = false;
      this.mapa.forEach((m) => {
        m.classList.remove('opacidad_mapa');
      });
    },
    detalleMapa(r) {
      this.$router.push(
        `/${this.eleccion}/DET/VPC/${r.getAttribute('finder')}`,
      );
      this.actualizarEstado(r);
      this.detalle.datos.elemento = r;
    },
    actualizarEstado(elemento) {
      this.detalle.esMapa = false;
      this.detalle.region = elemento;
      // this.detalle.esDiputaciones = this.esDiputaciones;
      this.$emit('childToParent', this.detalle);
    },
    actualizarTooltip(value) {
      this.tooltipActivo = value;
    },
    registerSvgPanZoom(svgpanzoom) {
      this.svgpanzoom = svgpanzoom;
    },
  },
  watch: {
    esMapa: {
      handler(val) {
        this.$nextTick(() => {
          if (val) {
            this.seleccionRegion = false;
            this.cargarEventos();
          }
        });
      },
      $nextTick: true,
    },
    // tooltipActivo: {
    //   handler(val) {
    //     if (!val) {
    //       this.mostrarRegion();
    //     }
    //   },
    // },
    // $route(to, from) {
    //   this.$nextTick(() => {
    //     this.cargarEventos();
    //   });
    // },
  },
  computed: {

    regionSeleccionada() {
      if (this.$route.params.eleccion === 'D') {
        return this.$store.state.dVotosMapaTarjetas;
      } if (this.$route.params.eleccion === 'A') {
        return this.$store.state.aVotosMapaTarjetas;
      }
    },
    esMapa() {
      return this.detalle.esMapa;
    },
    eleccion() {
      return this.$route.params.eleccion;
    },
    votosXPartidoGraficas() {
      if (this.$route.params.eleccion === 'G') {
        return this.$store.state.dVotosXActorPoliticoGraficas;
      } if (this.$route.params.eleccion === 'D') {
        return this.$store.state.dVotosXActorPoliticoGraficas;
      } if (this.$route.params.eleccion === 'A') {
        return this.$store.state.aVotosXActorPoliticoGraficas;
      }

      return {};
    },
    calcularTotal() {
      let total = 0;
      this.detalle.datos.datos_candidatos.map((partido) => {
        total += parseInt(
          this.eleccion === 'D'
            ? partido.candidatura_distritos_obtenidos_numero
            : partido.candidatura_municipios_obtenidos_numero,
        );
      });

      return total;
    },
    

  },
};
</script>

<style lang="scss" scoped>
  .table-responsive {
    thead {
      th {
        background-color:#582D73;
        color: #FFF;

        th {
          vertical-align: middle;
          img {
            max-height: 35px;
            /* max-width: 35px; */
          }
        }
      }
    }
    tbody {
      tr {

        td {

          vertical-align: middle;
          img {
            max-height: 50px;
          }
        }
        .votos {
          text-align: center;
          p {
            font-size: 2rem !important;
            font-weight: bold !important;
          }
        }
      }
    }
  }
.sombra {
background-color: #E97EBD;
}
.texto{
  b{
    font-size: 16px;
  }
}
.grilla {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  align-items: center;

  .tabla {
    .grilla-interna {
      display: grid;
      grid-template-columns: 1fr 1fr;
      text-align: center;
      align-items: center;

      .contenedor-imagen {
        border-left: 5px solid;
      }
    }
  }

  .mapa {
    display: block;
    position: relative;
    margin-top: -10px;

    .mensaje {
      max-width: 500px;
    }
  }
}



.foto-candidatura {
  max-height: 50px;
  /* max-width: 50px; */
}
.foto-candidatura-tabla {
  max-height: 35px;
  /* max-width: 50px; */
}
</style>
